import { mapState, mapGetters, mapActions } from 'vuex'
import { getCheckCardByNumber } from '@/utils/ApiList.js'
export default {
	data() {
		return {
			
		}
	},
	computed: {
	  	...mapState([
			'userData',
			'cardId',
			'openID',
			'nickName',
			'headImgUrl',
			'docUrl',
			'passwordExist',
			'supportRecover',
			'needUnbind'
		]),
		...mapGetters([
			'getHeadImgUrl',
			'getNickName'
		]),
		menuListCompute:function (){
			let arr = this.userData.menuList
			let requestArray = new Array
			if (arr.length>0) {
				arr.forEach(element => {
					if (element.text!='恢复上网' || this.supportRecover=='支持'){
						requestArray.push(element)
					}
				});
			}
			return requestArray
		}
	},
	filters:{
		pwdFilter:function (data,status) {
			if (!data) return ''
			if (data=='支付密码'){
				if (status==0){
					 return '支付密码'
				}else {
				    return '重置密码'
				}			  
			}else {
				return data
			}
		}
	},
	mounted: function(){
		if (this.cardId !='') {
			getCheckCardByNumber(this.cardId).then(res=> {
				console.log(res)
				if (res.statusCode == 200 && res.data.card_no!='') {
					this.asyncSetting({
						ispName:res.data.operator,
						docUrl:res.data.use_doc_url,
						passwordExist:res.data.password_exist,
						needUnbind:res.data.need_unbind,
						supportRecover:res.data.support_recover
					})
					this.updateBalanceBy(res.data)                
				}
			})
		}
		this.getUserOpenId()
	},
	methods: {
		navaToPage: function (path) {
			if (this.cardId == '' && path!='/change') {
				this.$router.push('/change')
			}else {
				if (path) {
					if (path=='/change') {
						if (this.cardId!='') {
							this.$router.push({
								path: '/set-meals',
								query: {
									state: this.cardId
								}
							})
						}else {
							this.$router.push({
								path: 'change',
								query: {
									target: '/set-meals'
								}
							})
						}
					}else if (path=='/set-password') {
						if (this.passwordExist !=1){
							this.$router.push(path)
						}else {
							this.$router.push('/change-pwd')
						}						
					// }else if (path=='/unbind'){
					// 	//
					}else {
						this.$router.push(path)
					}
				}	  
			}			
		},
		toHelp: function () {
			console.log(this.docUrl)
			if (this.cardId=='') {
				this.$alert('请先绑定卡号', '提示', {
					confirmButtonText: '确定',
				});
			}else if (this.docUrl=='') {
				this.$alert('此卡暂无使用说明', '提示', {
					confirmButtonText: '确定',
				});
			}else {
				window.location.href=this.docUrl
			}
		},
		...mapActions('userData',[
			'updateBalanceBy'
		]),
		...mapActions([
			'asyncSetting',
			'getUserOpenId'
		])
	}
}
